import React, { Component } from 'react';
import { connect } from "react-redux";
import SVG from 'react-inlinesvg';
import { Link } from 'react-router-dom';
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { getTemplateList, getTemplateListDispatch } from "../../../../../Services/Actions/templateAction";

const removeDuplicate = (data) => {
    try {
        var updateFilter = []
        var updateKey = {}
        data.map((item) => {
            if (!updateKey[item.id]) {
                updateFilter.push(item)
                updateKey[item.id] = true
            }

        })
        return updateFilter
    }
    catch (ex) {
        return data
    }
}

class Templates extends Component {
    state = {
        templeteList: [],
        isUpdate: false,
        selectTemp:null
    }
    componentWillMount() {setTimeout(() => {this.onLoad()}, 100)}
    componentDidMount() {setTimeout(() => {this.onLoad()}, 100)}
    onLoad = () => {
        var self = this
        const { templeteList, state } = this.props
        //if (templeteList && templeteList.length > 0) {
        //self.setState({ templeteList: removeDuplicate(templeteList), selectTemp: state.selectedTemplate })
        // }
        // else {
        //     getTemplateList({ type: 1 }).then((Response) => {
        //         const { responseData } = Response.data
        //         self.setState({ templeteList: removeDuplicate(responseData.templates) })
        //         self.props.getTemplateListDispatch(removeDuplicate(responseData.templates))
        //     })
        // }
        if(templeteList && templeteList.length >0){
            const filterTemplate = templeteList.filter(temp=> temp.Theme.type == 1)
            self.setState({ templeteList: removeDuplicate(filterTemplate), selectTemp: state.selectedTemplate && Object.keys(state.selectedTemplate).length > 0 ? state.selectedTemplate : filterTemplate[0] })
        }

    }
    onFiltertemplateName = (id) => {
        const { templeteList } = this.state;

        if (templeteList && templeteList.length > 0) {
            const updated = templeteList.filter((item) => item.id == id)
            if (updated && updated.length > 0) {
                return updated[0].name
            }
            else return ""
        }
        else return ""

    }

    render() {
        const { templeteList, selectTemp } = this.state;
        const { onSelectTemplate, state } = this.props
        return <>
            <label className="text-gray-800 mb-2">Select Template <strong className="text-danger">*</strong></label>
            <Accordion.Item eventKey="8" className="pb-3">
                <Accordion.Header className="w-100">
                    <span className="fs-9 fw-medium">{selectTemp ? selectTemp.name ? selectTemp.name : this.onFiltertemplateName(selectTemp.id) : `Please select template.`}</span>
                </Accordion.Header>
                <Accordion.Body className="border rounded-1 mt-1">
                    {templeteList && templeteList.length > 0 ?
                        <ToggleButtonGroup type="radio" className="flex-column w-100 pb-2" name="options">
                            {templeteList.map((temp, index) => {
                                return <><ToggleButton key={`template_id_${index}`} className={`p-0 card rounded-1 text-decoration-none cursor-pointer mb-2 ${(state.selectedTemplate && Object.keys(state.selectedTemplate).length > 0 && (temp.id == state.selectedTemplate.id)) ? `active` : ``}`} variant="outline-secondary" id="tbg-radio-1" value={index} onClick={()=>{this.setState({selectTemp:temp},()=>{onSelectTemplate(temp)})}}>
                                    <div className="card-body p-2">
                                        <div className="d-flex align-items-center">
                                            <div className="symbol symbol-40 flex-shrink-0 me-2 text-gray-400">
                                                <img className="img-fluid" src={baseUrl(`media/images/themes/${temp.Theme.id}.png`)} height="40" width="40" alt={temp.name} />
                                            </div>
                                            <div className="overflow-hidden text-start">
                                                <p className={`fs-8 fw-semibold mb-1 text-gray-dark text-ellipsis text-start`}>{temp.name}</p>
                                                <p className="fs-9 fw-semibold mb-0 text-gray-600 text-ellipsis">{temp.Theme.name}</p>
                                            </div>
                                        </div>
                                    </div>
                                </ToggleButton></>
                            })}
                        </ToggleButtonGroup>
                        : null}
                    <div className="text-center">
                        <Link to={'/template/add'} className="btn btn-link text-decoration-none btn-sm p-0 btn-min-w">
                            <i className="ri-add-line"></i>
                            Create New Template
                        </Link>
                    </div>
                </Accordion.Body>
            </Accordion.Item>

        </>
    }
}

const mapStateToProps = (state) => {
    return {
        templeteList: state.templates.templeteList
    }
}


const mapDispatchToProps = dispatch => {
    return {
        getTemplateListDispatch: (templates) => dispatch(getTemplateListDispatch(templates))

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Templates);